import Vue from 'vue'
import jquery from 'jquery'
import Router from 'vue-router'
import index from '@/components/index'
import BrandCulture from '@/components/BrandCulture'
import CustomStory from '@/components/CustomStory'
import ProductSeries from '@/components/ProductSeries'
import Store from '@/components/Store'
import MyBespeak from '@/components/MyBespeak'



Vue.use(Router)
export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/BrandCulture',
      name: 'BrandCulture',
      component: BrandCulture
    },
    {
      path: '/CustomStory',
      name: 'CustomStory',
      component: CustomStory
    },
    {
      path: '/ProductSeries',
      name: 'ProductSeries',
      component: ProductSeries
    },
    {
      path: '/Store',
      name: 'Store',
      component: Store
    },
    {
      path: '/MyBespeak',
      name: 'MyBespeak',
      component: MyBespeak
    }
  ]
})



